const onClickAdd = () => {
  // „ÉÜ„Ç≠„Çπ„Éà„Éú„ÉÉ„ÇØ„Çπ„ÅÆÂÄ§„ÇíÂèñÂæó„Åó„ÄÅÂàùÊúüÂåñ„Åô„Çã
  const inputText = document.getElementById("add-text").value;
  document.getElementById("add-text").value = "";

  //„É™„Çπ„ÉàËøΩÂä†„ÅÆÂÖ±ÈÄöÈñ¢Êï∞„Å´ÁΩÆ„ÅçÊèõ„Åà„Å¶„ÉÜ„Ç≠„Çπ„Éà„ÅØ„ÉÜ„Ç≠„Çπ„Éà„Éú„ÉÉ„ÇØ„Çπ„ÅÆÂÖ•Âäõ„Åó„Åü„ÉÜ„Ç≠„Çπ„Éà(inputText)„ÇíÂèñÂæó
  createIncompleteList(inputText);
};

//„ÄÄÊú™ÂÆå‰∫Ü„É™„Çπ„Éà„Åã„ÇâÊåáÂÆö„ÅÆË¶ÅÁ¥†„ÇíÂâäÈô§ (ÂÆå‰∫Ü„ÉªÂâäÈô§„Éú„Çø„É≥) //ÂÖ±ÈÄöÂåñ
const deleteFromIncompleteList = (target) => {
  document.getElementById("incomplete-list").removeChild(target);
};

//„ÄÄÊú™ÂÆå‰∫Ü„É™„Çπ„Éà„Å´ËøΩÂä†„Åô„ÇãÈñ¢Êï∞ //ËøΩÂä†„Åô„Çã„Å®„Åç„ÉªÊàª„Åô„Å®„Åç„ÅÆÂÖ±ÈÄöÂåñÂá¶ÁêÜ
const createIncompleteList = (text) => {
  // div„Çø„Ç∞ÁîüÊàê
  const div = document.createElement("div");
  div.className = "list-low";

  // li„Çø„Ç∞ÁîüÊàê
  const li = document.createElement("li");
  li.innerText = text;

  // button(ÂÆå‰∫Ü)„Çø„Ç∞ÁîüÊàê
  const completeButton = document.createElement("button");
  completeButton.innerText = "ÂÆå‰∫Ü";

  completeButton.addEventListener("click", () => {
    //Êäº„Åï„Çå„ÅüÂâäÈô§„Éú„Çø„É≥„ÅÆË¶™„Çø„Ç∞(div)„ÇíÊú™ÂÆå‰∫Ü„ÅÆ„É™„Çπ„Éà„Åã„ÇâÂâäÈô§(ÂÖ±ÈÄö„ÅÆÈñ¢Êï∞Âåñ„ÅßÊõ∏„ÅçÊèõ„Åà)
    deleteFromIncompleteList(completeButton.parentNode);
    // ÂÖ±ÈÄöÈñ¢Êï∞ÂåñÂâç„ÅÆË®òËø∞
    // const completeTarget = completeButton.parentNode;
    // document.getElementById("incomplete-list").removeChild(completeTarget);

    // ÂÆå‰∫Ü„É™„Çπ„Éà„Å´ËøΩÂä†„Åô„ÇãË¶ÅÁ¥†
    const addTarget = completeButton.parentNode;

    // TODOÂÜÖÂÆπ„ÉÜ„Ç≠„Çπ„Éà„ÇíÂèñÂæó
    const text = addTarget.firstElementChild.innerText;

    // div‰ª•‰∏ã„ÇíÂàùÊúüÂåñ
    addTarget.textContent = null; //ÂàùÊúüÂåñË®≠ÂÆö„ÅÆ„Åü„ÇÅnull„ÇíË®≠ÂÆö

    // complete-area„ÅÆli„Çø„Ç∞ÁîüÊàê
    const li = document.createElement("li");
    li.innerText = text;

    //„ÄÄcomplete-area„ÅÆbutton„Çø„Ç∞ÁîüÊàê
    const backButton = document.createElement("button");
    backButton.innerText = "Êàª„Åô";
    backButton.addEventListener("click", () => {
      //Êäº„Åï„Çå„ÅüÊàª„Åô„Éú„Çø„É≥„ÅÆË¶™„Çø„Ç∞„ÇíÂÆå‰∫Ü„É™„Çπ„Éà„Åã„ÇâÂâäÈô§
      const deleteTarget = backButton.parentNode;
      document.getElementById("complete-list").removeChild(deleteTarget);

      // „ÉÜ„Ç≠„Çπ„Éà„ÇíÂèñÂæó
      const text = backButton.parentNode.firstElementChild.innerText;
      createIncompleteList(text);
    });

    // div„Çø„Ç∞„ÅÆÂ≠êË¶ÅÁ¥†„Å´ÂêÑË¶ÅÁ¥†„ÇíË®≠ÂÆö
    addTarget.appendChild(li);
    addTarget.appendChild(backButton);

    //ÂÆå‰∫Ü„É™„Çπ„Éà„ÅÆ<ul class="complete-list">ÈÖç‰∏ã„Å´ËøΩÂä†
    document.getElementById("complete-list").appendChild(addTarget);
  });

  // button(ÂâäÈô§)„Çø„Ç∞ÁîüÊàê
  const deleteButton = document.createElement("button");
  deleteButton.innerText = "ÂâäÈô§";
  deleteButton.addEventListener("click", () => {
    //Êäº„Åï„Çå„ÅüÂâäÈô§„Éú„Çø„É≥„ÅÆË¶™„Çø„Ç∞(div)„ÇíÊú™ÂÆå‰∫Ü„ÅÆ„É™„Çπ„Éà„Åã„ÇâÂâäÈô§(ÂÖ±ÈÄö„ÅÆÈñ¢Êï∞Âåñ„ÅßÊõ∏„ÅçÊèõ„Åà)
    deleteFromIncompleteList(deleteButton.parentNode);

    // ÂÖ±ÈÄöÈñ¢Êï∞ÂåñÂâç„ÅÆË®òËø∞
    // const deleteTarget = deleteButton.parentNode;
    // document.getElementById("incomplete-list").removeChild(deleteTarget);
  });

  //div„Çø„Ç∞„ÅÆÂ≠êË¶ÅÁ¥†„Å´ÂêÑË¶ÅÁ¥†„ÇíË®≠ÂÆö
  div.appendChild(li);
  div.appendChild(completeButton);
  div.appendChild(deleteButton);

  // Êú™ÂÆå‰∫Ü„É™„Çπ„Éà„Å´ËøΩÂä†
  document.getElementById("incomplete-list").appendChild(div);
};

document
  .getElementById("add-button")
  .addEventListener("click", () => onClickAdd());
